// @mui
import { useEffect, useState } from 'react';
// components
import { useSettingsContext } from 'src/components/settings';
import DataTable from 'react-data-table-component';
import { Button, IconButton, InputAdornment, Container, Typography, Menu, MenuItem, TextField, Dialog, DialogTitle, DialogContent, DialogActions, Modal, Grid, Box, FormControl, InputLabel, Select } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import axios, { endpoints } from 'src/utils/axios';
import { constants } from 'src/utils/constant';
import { useSnackbar } from 'src/components/snackbar';
import { decryptToken, color, formatDate } from 'src/utils/common';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import AddIcon from '@mui/icons-material/Add';
import { SingleInputDateRangeField } from '@mui/x-date-pickers-pro/SingleInputDateRangeField';
import dayjs from 'dayjs';
import { useNavigate } from 'react-router';
import { LocalizationProvider } from '@mui/x-date-pickers';
import ModeEditIcon from '@mui/icons-material/ModeEdit';
import DeleteIcon from '@mui/icons-material/Delete';
import * as Yup from 'yup';
import { LoadingButton } from '@mui/lab';
import { useAuthContext } from 'src/auth/hooks';
// ----------------------------------------------------------------------

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '50%',
    bgcolor: 'background.paper',
    borderRadius: '10px',
    boxShadow: 24,
    p: 2,
    backgroundColor: '#E6F2FF',
    height: 'auto',
    overflow: 'auto',
};

export default function UserView() {
    const { user: authUser } = useAuthContext();

    const settings = useSettingsContext();
    const { enqueueSnackbar } = useSnackbar();
    const navigate = useNavigate();
    const [searchQuery, setSearchQuery] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [data, setData] = useState([]);
    const [value, setValue] = useState(() => [
        dayjs('2022-04-17'),
        dayjs('2022-04-21'),
    ]);
    const [isSending, setIsSending] = useState(false);

    const [editUser, setEditUser] = useState(false);
    const [userName, setUserName] = useState('');
    const [role, setRole] = useState('');
    const [email, setEmail] = useState('');
    const [users, setUsers] = useState([]);
    const [userIndex, setUserIndex] = useState(null);

    // For Modal Open
    const [open, setOpen] = useState(false);

    const [isAdded, setIsAdded] = useState(false);

    const handleOpen = () => {
        setOpen(true);
        setEditUser(false);
    };

    // For Actions Dropdown
    const [anchorEl, setAnchorEl] = useState(null);
    const openMenu = Boolean(anchorEl);
    // Temporary State
    const [tempData, setTempData] = useState({});
    const [openDialog, setOpenDialog] = useState(false);

    const fetchData = async () => {

        try {
            const decryptedToken = localStorage.getItem(constants.keyUserToken);
            const accessToken = decryptToken(decryptedToken);
            const response = await axios.post(endpoints.user.list, {}, {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            });
            if (response) {
                setIsLoading(false);
                setData(response.data.data);
            };

        } catch (err) {
            enqueueSnackbar('Failed to fetch records!', { variant: 'error' });
            setIsLoading(false);
        }
    };

    const handleRedirectionToUserDetailsPage = () => {
        navigate('/dashboard/user/details', { state: tempData });
    }
    const handleConfirmDelete = () => {
        console.log('tempdata', tempData);
        const decryptedToken = localStorage.getItem(constants.keyUserToken);
        const accessToken = decryptToken(decryptedToken);
        const config = {
            headers: {
                Authorization: `Bearer ${accessToken}`
            }
        };

        axios.post(endpoints.user.delete, { id: tempData._id }, config)
            .then((res) => {
                if (res) {
                    enqueueSnackbar('User deleted successfully!', { variant: 'success' });
                    setOpenDialog(false);
                    setTempData(null);
                    fetchData();
                };
            }).catch((err) => {
                enqueueSnackbar(err.error || 'Something went wrong!', { variant: 'error' });
            })
    };

    useEffect(() => {
        setIsLoading(true);
        fetchData();

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const columns = [
        {
            name: 'Name',
            selector: (row) => row.name,
        },
        {
            name: 'Email Address',
            selector: (row) => row.email,
        },
        {
            name: 'User Role',
            selector: (row) => row.roleId.name,
        },
        {
            name: 'Status',
            selector: (row) => row.isActive ? 'Active' : 'Inactive',
        },
        {
            name: 'Added On',
            selector: (row) => new Date(row.createdAt).toLocaleDateString('en-GB', { day: '2-digit', month: 'long', year: 'numeric' }),
            sortable: 'true',
            width: "220px"
        },
        {
            name: 'Actions',
            cell: (row) => (
                <>
                    <Button
                        disabled={!authUser?.permissions?.sales?.u}

                        onClick={(event) => {
                            setAnchorEl(event.currentTarget);
                            setTempData(row)
                        }} style={{ color: color.actionButton }} variant='outlined' endIcon={<ArrowDropDownIcon />}>Action</Button>
                    <Menu
                        id="basic-menu"
                        anchorEl={anchorEl}
                        open={openMenu}
                        onClose={() => {
                            setAnchorEl(null);
                            setTempData({});
                        }}
                        MenuListProps={{
                            'aria-labelledby': 'basic-button',
                        }}
                    >
                        <MenuItem onClick={handleRedirectionToUserDetailsPage}>Edit User Details</MenuItem>
                        <MenuItem onClick={() => {
                            setOpenDialog(true);
                            setAnchorEl(null);
                        }}>Delete</MenuItem>

                    </Menu>
                </>
            ),
        },
    ];

    const filteredRecords = data.filter(filteredData => filteredData?.name?.toLowerCase().includes(searchQuery?.toLowerCase()));


    const userSchema = Yup.object().shape({
        name: Yup.string().required('Name is required').matches(/^[A-Za-z\s]+$/, 'Name must contain only letters and spaces'),
        email: Yup.string().required('Email is required').email('Invalid email address'),
        role: Yup.string().required('Role is required')
    });


    const handleAdd = () => {
        // Check user name  in users array and if it's already then show a error
        if (users.find(user => user.name === userName)) {
            enqueueSnackbar('User already added!', { variant: 'error' });
            return;
        };

        if (users.find(user => user.email === email)) {
            enqueueSnackbar('Email already added!', { variant: 'error' });
            return;
        };

        // Validate userSchema and then proceed
        userSchema.validate({ name: userName, email, role })
            .then(() => {
                setIsAdded(true);
                setUsers([...users, { name: userName, role, email }]);
                setUserName('');
                setRole('');
                setEmail('');
            })
            .catch((err) => {
                enqueueSnackbar(err.errors[0], { variant: 'error' });
            });
    };

    const handleUpdate = () => {
        // Check user name  in users array and if it's already then show a error
        const remainingUsers = users.filter((user, index) => index !== userIndex);
        if (remainingUsers.find(user => user.name === userName)) {
            enqueueSnackbar('User already added!', { variant: 'error' });
            return;
        };

        if (remainingUsers.find(user => user.email === email)) {
            enqueueSnackbar('Email already added!', { variant: 'error' });
            return;
        };

        // Validate userSchema and then proceed
        userSchema.validate({ name: userName, email, role })
            .then(() => {
                setIsAdded(true);
                const tempUsers = users.map((user, i) => {
                    if (i === userIndex) {
                        return { name: userName, role, email };
                    }
                    return user;
                });
                setUsers(tempUsers);
                setUserName('');
                setRole('');
                setEmail('');
                setUserIndex(null);
            })
            .catch((err) => {
                enqueueSnackbar(err.errors[0], { variant: 'error' });
            });

    };

    const handleSendInvitation = () => {
        console.log(users)
        setIsSending(true);
        // Call API
        const decryptedToken = localStorage.getItem(constants.keyUserToken);
        const accessToken = decryptToken(decryptedToken);
        const config = {
            headers: {
                Authorization: `Bearer ${accessToken}`
            }
        }
        axios.post(endpoints.user.invite, { users }, config)
            .then((res) => {
                if (res) {
                    enqueueSnackbar('Invitation sent successfully!', { variant: 'success' });
                    setOpen(false);
                    setUserName('');
                    setRole('');
                    setEmail('');
                    setUsers([]);
                    setIsAdded(false);
                    setIsSending(false);
                };
            }).catch((err) => {
                setIsSending(false);
                enqueueSnackbar(err.error || 'Something went wrong!', { variant: 'error' });
            })
    }

    return (
        <Container maxWidth={settings.themeStretch ? false : 'xl'}>
            <Typography variant="h4"> User Management </Typography>
            <div style={{ display: 'flex', flexDirection: 'flex-end', justifyContent: 'space-between', alignItems: 'center' }}>

                <div style={{ display: 'flex', justifyContent: 'start', alignItems: 'center', marginTop: '20px', gap: '10px' }}>
                    <TextField
                        size="small"
                        onChange={(e) => setSearchQuery(e.target.value)}
                        type="text"
                        placeholder="Search by name"
                        InputProps={{
                            startAdornment: (
                                <InputAdornment position="start">
                                    <SearchIcon />
                                </InputAdornment>
                            ),
                        }}
                    />
                </div>
                {
                    authUser?.permissions?.sales?.u &&
                    <div>
                        <Button startIcon={<AddIcon />} color='primary' variant='contained' onClick={handleOpen}>
                            ADD NEW USER
                        </Button>
                    </div>
                }

            </div>

            <div style={{ marginTop: "20px" }}>
                <DataTable
                    columns={columns}
                    data={filteredRecords}
                    pagination
                    progressPending={isLoading}
                />
            </div>

            {/* Dialog For Delete */}
            <Dialog open={openDialog} onClose={() => setOpenDialog(false)}>
                <DialogTitle>Remove User</DialogTitle>
                <DialogContent>
                    Are you sure you want to delete this user?
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => {
                        setOpenDialog(false);
                        setTempData(null);
                    }}>Cancel</Button>
                    <Button onClick={handleConfirmDelete} variant="contained" color="error">
                        Delete
                    </Button>
                </DialogActions>
            </Dialog>

            {/* INVITE MODAL */}
            <Modal
                open={open}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <div >
                        <Typography variant="h5">Invite new members</Typography>
                    </div>

                    <div style={{ maxHeight: '500px', overflow: 'auto' }}>

                        {
                            users.length > 0 &&
                            <div style={{ display: 'flex', flexDirection: 'column', gap: '10px', borderRadius: '8px', padding: '20px' }}>
                                <div>
                                    <Typography variant="h6">Added Users</Typography>
                                </div>
                                <div style={{ display: 'flex', flexDirection: 'column', gap: '20px', flexWrap: 'wrap' }}>
                                    {
                                        users.map((user, index) => (
                                            <div key={index} style={{ display: 'flex', flexDirection: 'row', gap: '5px', justifyContent: 'space-between' }}>
                                                <div>
                                                    <Typography variant="subtitle1">{`${user.name} - ${user.role} `}</Typography>
                                                    <Typography variant="caption">{user.email}</Typography></div>
                                                <div>
                                                    <Button color='success' onClick={
                                                        () => {
                                                            console.log('calling edit')
                                                            setEditUser(true);
                                                            setUserName(user.name);
                                                            setRole(user.role);
                                                            setEmail(user.email);
                                                            setIsAdded(false);
                                                            setUserIndex(index)
                                                        }
                                                    }>
                                                        <ModeEditIcon />
                                                    </Button>
                                                    <Button color='error' onClick={
                                                        () => {
                                                            const tempUsers = users.filter((item, i) => i !== index);
                                                            setUsers(tempUsers);
                                                        }

                                                    }>
                                                        <DeleteIcon />
                                                    </Button>
                                                </div>

                                            </div>
                                        ))
                                    }
                                </div>
                            </div>
                        }


                        {
                            !isAdded &&

                            <div style={{ marginTop: '10px', padding: '20px', display: 'flex', flexDirection: 'column', gap: '10px', backgroundColor: 'white', borderRadius: '8px' }}>
                                <div style={{ display: 'flex', flexDirection: 'row', gap: '20px' }} >
                                    <TextField
                                        type="text"
                                        placeholder="Enter Name"
                                        label="Name"
                                        value={userName}
                                        onChange={(e) => setUserName(e.target.value)}
                                        fullWidth
                                    />
                                    <FormControl fullWidth>
                                        <InputLabel id="demo-simple-select-label">Role</InputLabel>
                                        <Select
                                            labelId="demo-simple-select-label"
                                            id="demo-simple-select"
                                            value={role}
                                            onChange={(e) => setRole(e.target.value)}
                                            label="Role"
                                        >
                                            <MenuItem value='ADMIN'>ADMIN</MenuItem>
                                            <MenuItem value='MANAGER'>MANAGER</MenuItem>
                                        </Select>
                                    </FormControl>
                                </div>


                                <div>
                                    <TextField
                                        type="text"
                                        placeholder="Enter email address"
                                        label="Email Address"
                                        value={email}
                                        onChange={(e) => setEmail(e.target.value)}
                                        fullWidth
                                    />
                                </div>
                                <div style={{ display: 'flex', justifyContent: 'flex-end', gap: '10px' }}>
                                    <Button variant='outlined' onClick={() => {
                                        setEditUser(false);
                                        setUserName('');
                                        setRole('');
                                        setEmail('');
                                    }}>
                                        CANCEL
                                    </Button>
                                    <Button variant='contained' color='primary' onClick={
                                        editUser ? handleUpdate : handleAdd
                                    }>
                                        {editUser ? 'UPDATE' : 'ADD'}
                                    </Button>
                                </div>

                            </div>
                        }
                    </div>


                    <div style={{ padding: '10px' }}>
                        <Button variant='outlined' onClick={() => setIsAdded(false)}>
                            ADD ANOTHER
                        </Button>
                    </div>


                    <div style={{ display: 'flex', paddingTop: '30px', justifyContent: 'flex-end', gap: '10px' }}>
                        <Button variant='outlined' onClick={
                            () => {
                                setOpen(false);
                                setIsAdded(false);
                                setUserName('');
                                setRole('');
                                setEmail('');
                                setUsers([]);
                            }
                        }>
                            CANCEL
                        </Button>
                        <LoadingButton variant='contained' color='primary' onClick={
                            handleSendInvitation
                        } loading={isSending}>
                            SEND INVITATION
                        </LoadingButton>
                    </div>
                </Box>
            </Modal>
        </Container >
    );
}
